.home {
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: black; */
}

.intro {
    max-width: 540px;
    /* background-color: antiquewhite; */
    text-align: center;
}

.home__img {
    margin-bottom: 1.5rem;
}

.home__name {
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 0.5rem;
}

.home__socials {
    display: flex;
    column-gap: 1.75rem;
    margin: 1.5rem 0;
    justify-content: center;
}

.home__social-link {
    color: var(--title-color);
    font-size: 1.3rem;
    transition: .5s;
    z-index: 3;
}

.home__social-link:hover {
    color: hsl(194, 78%, 56%);
}

.scroll__down {
    position: absolute;
    bottom: 2.5rem;
    left:0;
    width:100%;
}

.home__scroll-name {
    font-size: var(--small-font-size);
}

.mouse {
    border: 2px solid #454360;
    display: block;
    height: 1.6rem;
    width: 1.25rem;
    margin:auto;
    margin-top:0.75rem;
    border-radius: 1rem;
    position: relative;
}

@keyframes ani-mouse {
    0% {
        top:30%;
    }
    15% {
        top:45%;
    }
    50% {
        top:50%;
    }
    100% {
        top:30%;
    }
}

.wheel {
    background-color: var(--title-color);
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top:.5rem;
    left:50%;
    transform: translateX(-50%);
    animation: ani-mouse 2s linear infinite;
}

.shapes {
    position: absolute;
    left:0;
    top:0;
    height: 100%;
    width: 100%;
}

